import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { SupportService } from '../support.service';
import { ActivatedRoute } from '@angular/router';
import { isEmpty } from 'lodash';
import { ExaTask } from '../../../core/providers/exatask.service';

@Component({
  selector: 'app-support-pages',
  templateUrl: 'page.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [SupportService]
})
export class PageComponent implements OnInit {

  page: string = '';
  pageHtml: string = '';

  pageTitles: Map<string, string> = new Map<string, string>();

  constructor(
    private route: ActivatedRoute,
    private supportService: SupportService,
    public exatask: ExaTask
  ) {

    this.pageTitles.set('contact-us', 'Contact Us');
    if (this.route.snapshot.params.page) {
      this.page = this.route.snapshot.params.page;
    }
  }

  async ngOnInit() {

    if (isEmpty(this.page)) {
      return;
    }

    this.exatask.setTitle(this.pageTitles.get(this.page));
    this.supportService.getSupportPage(this.page).then(page_data => {
      this.pageHtml = page_data;
    }).catch(err => {
    });
  }
}
