import { Injectable } from '@angular/core';
import { Api } from 'core/services/api.service';
import { clone } from 'lodash';

@Injectable()
export class SupportService extends Api {

  private _base_url: string = '/pages/support';

  getSupportPage(page: string): any {

    const query = new Map<string, any>();
    query.set('page', page);

    let headers = clone(this.headers);
    headers = headers.set('Accept', 'text/html');

    const config = new Map<string, any>();
    config.set('headers', headers);

    return this.get(this._base_url, query, config)
      .then(response => response);
  }
}
